/**
 * Breakpoints
 */
$container-gap: 20rem;

$breakpoints: (
	frame:		1920px,
	container:	1120px,
	large-3x:	2560px,		// laptop 4K
	large-2x:	1440px,		// laptop large
	large-1x:	1024px,		// laptop
	large:		768px,		// tablet
	medium:		425px,		// mobile large
	small:		375px,		// mobile medium
	small-1x:	320px,		// mobile small
	small-2x:	280px,		// mobile small-1x
);


// 이하
@mixin respond-below($breakpoint) {
	@if map-has-key($breakpoints, $breakpoint) {
		$breakpoint-value: map-get($breakpoints, $breakpoint);
		@media (max-width: ($breakpoint-value - 1)) {
			@content;
		}
	} @else {
		@warn 'Invalid breakpoint: #{$breakpoint}.';
	}
}


// 이상
@mixin respond-above($breakpoint) {
	@if map-has-key($breakpoints, $breakpoint) {
		$breakpoint-value: map-get($breakpoints, $breakpoint);
		@media (min-width: $breakpoint-value) {
			@content;
		}
	} @else {
		@warn 'Invalid breakpoint: #{$breakpoint}.';
	}
}

@mixin respond-between($lower, $upper) {
	@if map-has-key($breakpoints, $lower) and map-has-key($breakpoints, $upper) {
		$lower-breakpoint: map-get($breakpoints, $lower);
		$upper-breakpoint: map-get($breakpoints, $upper);
		@media (min-width: $lower-breakpoint) and (max-width: ($upper-breakpoint - 1)) {
			@content;
		}
	} @else {
		@if (map-has-key($breakpoints, $lower) == false) {
			@warn 'Your lower breakpoint was invalid: #{$lower}.';
		}
		@if (map-has-key($breakpoints, $upper) == false) {
			@warn 'Your upper breakpoint was invalid: #{$upper}.';
		}
	}
}


/**
 * Vertical rhythm
 */

$vertical-rhythm: (
	laptop: (
		content: 8rem,
		section: 4rem,
		subsection: 3rem,
		subsubsection: 2rem,
	),
	mobile: (
		content: 4rem,
		section: 2.5rem,
		subsection: 2rem,
		subsubsection: 1.5rem,
	)
);

/**
 * Gutter
 */

$gutter: (
	laptop: (
		content: 4rem,
		section: 3rem,
		subsection: 2rem,
		subsubsection: 1.5rem,
	),
	mobile: (
		content: 1.5rem,
		section: 1.5rem,
		subsection: 1rem,
		subsubsection: 1rem,
	)
);


/**
 * Color theme creation
 */

// color palettes
$color-palettes: (
	base: (
		100: #000000,
		090: #191919,
		080: #313233,
		070: #4a4b4c,
		060: #636466,
		050: #7c7d7f,
		040: #949599,
		030: #adaeb2,
		020: #dcdce0,
		010: #ecedf0,
		005: #f6f6f9,
		000: #ffffff,
	),

	primary: (
		100: #000000,
		090: #5a080d,
		080: #7c0c12,
		070: #b5111a,
		060: #e21521,
		050: #e8444d,
		040: #ee737a,
		030: #f3a1a6,
		020: #f9ccce,
		000: #ffffff,
	),

	secondary: (
		100: #000000,
		090: #0f1526,
		080: #21283f,
		070: #2a3353,
		060: #3a456d,
		050: #4d577b,
		040: #7b84a3,
		030: #abafbf,
		020: #cdd0e0,
		010: #e5e7ef,
		000: #ffffff
	),

	normal: (
		100: #000000,
		010: #191919,
		020: #4a4b4c,
		030: #7c7d7f,
		040: #adaeb2,
		050: #ffffff,
		060: #ffffff,
		070: #ffffff,
		080: #ffffff,
		090: #ffffff,
		000: #ffffff,
	),

	accent: (
		100: #000000,
		010: #e21521,
		020: #323b5d,
		030: #0f1526,
		040: #abafbf,
		050: #ffffff,
		060: #ffffff,
		070: #ffffff,
		080: #ffffff,
		090: #ffffff,
		000: #ffffff,
	),
);

// Create Color
@function color($theme: normal, $variant: 00, $opacity: 1) {
	$set-color: map-get($color-palettes, $theme, $variant);
	$create-color: rgba(red($set-color),green($set-color),blue($set-color),$opacity);
	@return $create-color;
}


/*
 * @ text accent
 * @ $text-color
 */
$text-accent: (
	normal-00: color(normal, 100),
	normal-01: color(normal, 010),
	normal-02: color(normal, 020),
	normal-03: color(normal, 030),
	normal-04: color(normal, 040),
	normal-10: color(normal, 000),
	accent-01: color(accent, 010),
	accent-02: color(accent, 020),
	accent-03: color(accent, 030),
	accent-04: color(accent, 040),
);



