html:not(.active-page-navi):not(.active-scroll-start) {
	.page-head {
		&::before {
			background-color: transparent;
		}
	}
}

	
@at-root html.laptop.disabled-scroll {
	body {

		overflow: hidden;
		// laptop
		@include respond-above(large-1x) {
		}
	}
}

.page-body.page-main {	

	#local {
		width: 100%;

		.local-head {
			@include hidden;
		}

		.local-body {
			padding: 0;
		}
	}
}

.page-foot {
	.section.widget-toolbar {
		transform: translateY(0);

		.section-wrap {
			.section-body {
				.navi-list {
					.navi-item.goto-top {
						.navi-text:not(.active) {
							background-color: color(accent, 030);
							
							svg {
								transform: rotate(180deg);
							}
						}
					}
				}
			}
		}
	}
}

.bui-full-page-pagination-list {
	display: flex;
	flex-direction: column;
	row-gap: 12rem;

	position: fixed;
	z-index: 1;
	right: 20rem;
	top: 50%;
	transform: translate(0, -50%);

	@at-root html.mobile & {
		display: none;
	}
	
	// laptop
	@include respond-above(large-1x) {
		row-gap: 20rem;
		right: 80rem;
	}
	
	.bui-full-page-pagination-item {
		width: 6rem;
		height: 6rem;
		border-radius: 3rem;
		background-color: color(accent, 040, 0.5);
		transition: height 0.25s ease;
		
		&.current {
			height: 22rem;
			background-color: color(accent, 010);
		}

		// laptop
		@include respond-above(large-1x) {
			width: 8rem;
			height: 8rem;
			border-radius: 4rem;

			&.current {
				height: 28rem;
			}
		}

		.bui-full-page-pagination-text {
			@include hidden;
		}
	}
}

/**
 * @type display
 * @name billboard
 */
 .display-board.billboard {
	position: relative;
	width: 100vw;
	left: calc(50% - 50vw);

	.board-list {
		.board-item {
			padding: 0 20rem;
			box-sizing: border-box;
			background-repeat: no-repeat;
			background-position: center;
			background-size: cover;
			background-image: var(--background-image);
			overflow: hidden;

			&[aria-label="1 / 3"] {

				.board-wrap {
					&::before {
						content: '';
						display: block;
						position: absolute;
						left: 20rem;
						bottom: 88rem;
						width: 100%;
						height: 80rem;
						background-image: url(./../images/main/billboard_01.svg);
						background-repeat: no-repeat;
						background-position: left;
						background-size: auto 100%;
	
						// laptop
						@include respond-above(large-1x) {
							right: 80rem;
							bottom: 140rem;
							left: 80rem;
							height: 106rem;
						}
					}
				}
			}

			// laptop
			@include respond-above(large-1x) {
				padding: 0 80rem;
			}

			.board-wrap {
				display: flex;
				align-items: center;
				@include section-padding(top);
				@include section-padding(bottom);
				position: relative;
				min-height: 100vh;
				box-sizing: border-box;

				.board-head {
					display: flex;
					flex-direction: column;
					position: relative;
					padding-bottom: 92rem;

					// laptop
					@include respond-above(large-1x) {
						padding-bottom: 116rem;
					}

					.board-subject {
						margin: 0;
						@include font((outline: body, size: large-2x, weight: light, family: openSans)...);
						color: color(accent, 030);

						// laptop
						@include respond-above(large-1x) {
							@include font((outline: body, size: large-7x)...);
						}

						.board-name {
							.wbr {
								display: block;
								
								&:nth-child(2) {
									@include font((family: default)...);
									font-weight: 600;
								}
							}
						}
					}
					.button-display.module-a {
						margin-top: 20rem;

						// laptop
						@include respond-above(large-1x) {
							margin-top: 32rem;
						}

						.button-area {
							.btn {
								box-shadow: 2px 4px 14px rgba(0, 0, 0, 0.1);
							}
						}
					}
					
				}
			}
		}
	}

	.swiper-control {
		position: absolute;
		top: auto;
		bottom: calc(50% - (246rem / 2) - 4rem);
		right: 20rem;
		z-index: 1;
		width: 28rem;
		height: 28rem;

		// laptop
		@include respond-above(medium) {
			bottom: calc(50% - (246rem / 2) - 4rem);
			right: auto;
			left: 103rem;
		}

		// laptop
		@include respond-above(large-1x) {
			bottom: calc(50% - (372rem / 2) - 4rem);
			left: 326rem;
		}

		.btn {
			display: block;
			position: relative;
			width: 28rem;
			height: 28rem;
			background-color: color(accent, 030, 0.1);
			border-radius: 50%;
			cursor: pointer;

			&::before {
				content: '';
				display: block;
				position: absolute;
				top: 0;
				right: 0;
				bottom: 0;
				left: 0;
			}

			&[aria-pressed="false"] {
				&::before {
					background: icon(pause, color(accent, 030)) no-repeat center/cover;
				}
			}
			&[aria-pressed="true"] {
				&::before {
					background: icon(play, color(accent, 030)) no-repeat center/cover;
				}
			}
		}
	}

	.swiper-pagination {
		display: flex;
		column-gap: 20rem;
		position: absolute;
		z-index: 1;
		top: auto;
		bottom: calc(50% - (246rem / 2));
		left: 20rem;

		.swiper-pagination-bullet {
			display: block;
			cursor: default;

			@include font((outline: body, size: small-2x, weight: bold)...);
			color: color(normal, 100, 0.3);
			
			&.swiper-pagination-bullet-active {
				color: color(normal, 100);
				text-decoration: underline;
			}
		}

		// laptop
		@include respond-above(large-1x) {
			bottom: calc(50% - (372rem / 2));
			left: 80rem;
		}
	}

	.swiper-progressbar {
		position: absolute;
		z-index: 1;
		top: auto;
		bottom: calc(50% - (246rem / 2) + 44rem);
		left: 20rem;
		width: 480rem;
		max-width: calc(100vw - 40rem);
		height: 2rem;
		background-color: color(accent, 030, 0.1);


		&::before {
			content: '';
			display: block;
			height: 2rem;
			width: var(--swiper-progess);
			background-color: color(accent, 030);
			transition: width 0.25s ease;
		}
		
		// laptop
		@include respond-above(large-1x) {
			bottom: calc(50% - (372rem / 2) + 36rem);
			left: 80rem;
			width: 270rem;
		}
	}
 }


 /**
 * @type section.module-a
 * @name who-we-are
 */
 .subsection.module-c {
	box-sizing: border-box;
	margin: 0;

	.subsection-wrap {
		.subsection-head {
			.subsection-subject {
				margin: 0 0 24rem 0;
				@include font((outline: body, size: small-4x, weight: bold, family: openSans)...);
				color: color(accent, 010);
				letter-spacing: 3rem;

				.subsection-name {
					display: block;
				}

				// laptop
				@include respond-above(large-1x) {
					margin: 0 0 32rem 0;
					@include font((outline: body, size: small-2x)...);
				}
			}
			.subsection-summary {
				margin: 0;
				@include font((outline: head, size: medium, weight: extra-light)...);
				color: color(normal, 010);

				// laptop
				@include respond-above(large-1x) {
					@include font((outline: head, size: large-4x)...);
				}

				.wbr {
					display: block;

					.em {
						font-weight: 600;
					}
				}
			}
		}
		
		.subsection-body {
			margin-top: 16rem;
			@include font((outline: body, size: small-1x, weight: light)...);
			color: color(accent, 030);

			// laptop
			@include respond-above(large-1x) {
				margin-top: 20rem;
				@include font((outline: body, size: small)...);
			}

			.para {
				margin: 8rem 0;

				.wbr {
					display: block;

					.em {
						font-weight: 400;
					}
				}
			}
		}

		.subsection-util {
			margin-top: 24rem;

			// laptop
			@include respond-above(large-1x) {
				margin-top: 64rem;
			}
		}
	}
}


.section.about-business {
	.section-wrap {
		.section-head {
			@include hidden;
		}
	}

	.section-body {
		.subsection {
			+.subsection {
				position: relative;
				box-shadow: inset 0 1rem 0 0 color(base, 020);

				&::before {
					content: '';
					display: block;
					position: absolute;
					top: 0;
					left: calc(50% - 50vw);
