}

.section.people-story {
	display: flex;
	flex-direction: column;
	
	position: relative;
	
	.section-wrap {
		&::before {
			content: '';
			display: block;
			position: absolute;
			z-index: -1;
			top: 0;
			bottom: 0;
			right: 0;
			left: calc(50% - 50vw);
			width: 100vw;
			background: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 0.7) 100%, rgba(255, 255, 255, 0.68) 100%), linear-gradient(90deg, #FAEFF1 0%, #E8E4EA 48.12%, #DFE3EC 100%);;
		}

		.section-head {
			@include hidden;
		}
	}
 }

.subsection.who-we-are {
	display: flex;
	align-items: center;
	@include section-padding(top);
	@include section-padding(bottom);
	
	.subsection-wrap {
		display: flex;
		flex-direction: column;
		justify-content: center;
		position: relative;

		 &::after {
			 content: '';
			 flex-shrink: 0;
			 display: block;
			 position: absolute;
			 left: 0;
			 bottom: 0;
			 width: calc(100vw - 20rem);
			 height: 192rem;
			 background: url(../images/main/enterprise_01.png) no-repeat center/cover;
			 border-top-left-radius: 260rem;
			 border-bottom-left-radius: 260rem;
		 }
	 }

	// mobile
	@include respond-below(large) {
		.subsection-wrap {
			padding-bottom: calc(192rem + 32rem);
		}
	}

	// tablet
	@include respond-above(large) {
		.subsection-wrap {
			padding-right: 60%;
			
			&::after {
				position: absolute;
				bottom: 50%;
				left: 40%;
				width: calc(100vw - 40%);
				height: 232rem;
				transform: translateY(50%);
			}
		}
	}
	
	// laptop
	@include respond-above(large-1x) {
		min-height: 100vh;

		// &:not(.active-animation) {
		// 	.subsection-wrap {
		// 		transform: translateY(100vh);
		// 	}
		// }

		.subsection-wrap {
			transition-delay: 0.5s;
			padding-right: 40%;

			&::after {
				left: 60%;
				width: calc(100vw - 60%);
				height: 520rem;
			}
		}
	}
 }

.subsection.what-we-do {
	display: flex;
	align-items: center;
	@include section-padding(top);
	@include section-padding(bottom);

	// laptop
	@include respond-above(large-1x) {
		min-height: 100vh;
	}

	.subsection-wrap {
		display: flex;
		flex-direction: column;
		flex: 1 1 100%;

		// laptop
		@include respond-above(large-1x) {
			transition-delay: 0.5s;
		}

		.subsection-body {
			position: relative;
			margin-top: 48rem;

			// laptop
			@include respond-above(large-1x) {
				margin-top: 64rem;
			}

			.data-display.service {

				// mobile
				@include respond-below(large-1x) {
					display: flex;
					position: relative;
					left: -20rem;
					width: 100vw;
					padding: 0 20rem;
					box-sizing: border-box;
					overflow-x: scroll;
					-ms-overflow-style: none; /* IE and Edge */
					scrollbar-width: none; /* Firefox */

					&::-webkit-scrollbar {
						display: none; /* Chrome, Safari, Opera*/
					}
				}

				.data-list {
					.data-item {
						&:nth-child(1) {
							&::before {
								background-image: url(../images/main/product_01.png);
							}
						}
						&:nth-child(2) {
							&::before {
								background-image: url(../images/main/product_02.png);
							}
						}
						&:nth-child(3) {
							&::before {
								background-image: url(../images/main/product_03.png);
							}
						}
					}
				}
			}
		}
	}
}


.subsection.how-we-work {
	@include section-padding(top);
	
