@keyframes rollingitem1 {
	0% {
		transform: translateX(0);
	}
	50% {
		transform: translateX(-100%);
	}
	50.01%{
		transform: translateX(100%);
	}
	100%{
		transform: translateX(0);
	}
}

@keyframes rollingitem2 {
	0% {
		transform: translateX(0);
	}
	100% {
		transform: translateX(-200%);
	}
}


.subsection.our-story {
	@include section-padding(top);	
	padding-bottom: 144rem;

	// tablet
	@include respond-above(large-1x) {
		padding-bottom: 200rem;
	}
		
	// laptop
	@include respond-above(large-1x) {
		padding-bottom: 260rem;
	}

	.subsection-wrap {
		.subsection-body {
			margin-top: 48rem;

			// laptop
			@include respond-above(large-1x) {
				margin-top: 120rem;
			}

			>.data-display.module-c {
				>.data-list {
					// display: flex;
					// flex-direction: column;
					// row-gap: 48rem;
					// align-items: center;

					transition-timing-function : linear;

					>.data-item {
						width: 200rem;
						// transition-timing-function : ease-out;

						// tablet
						@include respond-above(large) {
							width: 288rem;
						}

						// laptop
						@include respond-above(large-1x) {
							width: 360rem;
						}

						.data-wrap {
							transform: translateY(0);
							transition: transform 0.25s ease-out;
						}

						&.odd {
