								}
							}
						}

						&.even {
							.data-wrap {
								transform: translateY(-40rem);

								// laptop
								@include respond-above(large-1x) {
									transform: translateY(-50rem);
								}
							}
						}

						.data-wrap {
							max-width: 100% !important;
						}
					}
				}
			}
		}
	}
}